import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'notificacao-dados',   templateUrl: 'notificacao_dados.component.html' }) export class NotificacaoDadosComponent implements OnInit {       displayMode: NotificacaoDadosDisplayModeEnum;     displayModeEnum = NotificacaoDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = NotificacaoDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = NotificacaoDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = NotificacaoDadosDisplayModeEnum.Edit;           break;       }     }  }  enum NotificacaoDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


